nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  background-color: #282c34;
  color: white;
}

ul {
  display: flex;
  list-style: none;
  margin-right: 10px;
}

.title {
  margin-left: 10px;
  font-size: 1.5rem;
}

.link {
  text-decoration: none;
  color: white;
  margin: 0 10px;
}

.cart-count {
  background-color: aliceblue;
  border-radius: 50%;
  padding: 0 5px;
  color: black;
  position: relative;
  text-align: center;
}

@media (min-width: 1279px) {
  .title {
    font-size: 2rem;
  }

  .link {
    font-size: 1.2rem;
  }
}
